@import url("https://fonts.googleapis.com/css2?family=Noto+Serif&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
*,
::after,
::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}

:root {
  --h1: 50px;
  --h2: 36px;
  --h3: 24px;
  --h4: 18px;
  --p1: 14px;
  --p2: 14px;
  --p3: 20px;
  --bg2: #2a2c39;
  --bg1: #252734;
  --white: #fff;
  --secondWhite: #d9d9d9;
  --gumbo: #60626d;
  --gumbo: #686c7c;
}
html::-webkit-scrollbar {
  background-color: var(--gumbo);
  width: 1vw;
}
html::-webkit-scrollbar-thumb {
  background-color: var(--bg2);
  border-radius: 30px;
}
html::-webkit-scrollbar-thumb:hover {
  background-color: var(--white);
}
html::-webkit-scrollbar-track {
  background-color: var(--gumbo);
  border-radius: 30px;
}
body {
  background-color: var(--bg1);
  font-family: "Poppins", Helvetica, sans-serif;
  width: 100%;
  overflow-x: hidden;
  /* border: 1px solid red; */
}
main.active {
  opacity: 0.2;
}
* {
  /* border: 1px solid yellow; */
}
h1,
h2 {
  font-family: "Times New Roman", Times, serif;
}

/**************************************************** hamburger *******************************************************/

.hamburger {
  width: 40px;
  height: 40px;
  display: none;
  justify-content: center;
  cursor: pointer;
}

.hamburger-menu {
  align-self: center;
  width: 30px;
  height: 1.6px;
  border-radius: 25px;
  background-color: var(--white);
  transition: 0.3s all;
}

.hamburger-menu::before,
.hamburger-menu::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 1.6px;
  border-radius: 25px;
  background-color: var(--white);
  transition: all 0.3s ease-out;
}

.hamburger-menu::before {
  transform: translateY(-9px);
}

.hamburger-menu::after {
  transform: translateY(9px);
}

.header-col-2.active .hamburger-menu::before {
  transform: translateY(0) rotate(45deg);
}

.header-col-2.active .hamburger-menu::after {
  transform: translateY(0) rotate(-45deg);
}

.header-col-2.active .hamburger-menu {
  transform: rotate(360deg);
  background-color: transparent;
}

/******************************************************* End hamburger *************************************************/
header {
  border-bottom: 1px solid var(--gumbo);
}

.header-div {
  margin: 0 auto;
  height: 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header-col1 {
  width: max-content;
  /* border: 1px solid pink; */
}
.logo {
  width: 100%;
  max-width: 70px;
}
.header-col2 {
  /* border: 1px solid red; */
  width: 70%;
}
.nav-list {
  width: 100%;
  /* border: 1px solid magenta; */
  list-style-type: none;
  display: flex;
  justify-content: flex-end;
  gap: 15px;
}
.nav-items {
  position: relative;
  padding: 10px 15px;
  cursor: pointer;
}
.items-link::after {
  content: "";
  position: absolute;
  left: 50%;
  right: 50%;
  width: 0;
  bottom: 0;
  background-color: var(--white);
  height: 2px;
  transition: all 400ms;
}
.nav-items:hover .items-link::after {
  left: 5%;
  right: 5%;
  width: 90%;
}
.nav-items:hover .items-link {
  color: var(--white);
}

.hide-items {
  display: none;
}
.items-link {
  /* border: 1px solid white; */
  font-size: var(--p3);
  text-decoration: none;
  color: var(--gumbo);
}

.header-col3 {
  /* border: 1px solid blue; */
  height: inherit;
  width: max-content;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.header-btn-div {
  position: relative;
}

.btn-bg {
  position: absolute;
  background-color: var(--white);
  opacity: 0;
  width: 0%;
  height: 0%;
  left: 50%;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 35px;
  transition: all 400ms;
}
.header-btn {
  position: relative;
  color: var(--white);
  text-decoration: none;
  font-size: calc(var(--p2) + 5px);
  border: 1px solid var(--gumbo);
  padding: 12px 25px;
  border-radius: 30px;
  cursor: pointer;
  z-index: 10;
}
a {
  text-decoration: none;
}
.header-btn-div:hover .btn-bg,
.header-btn-div:focus .btn-bg {
  opacity: 0.05;
  width: 100%;
  height: 100%;
  left: 0%;
  top: 0%;
  transform: translateY(0%);
}

@media (max-width: 1050px) {
  .header-div {
    height: 100px;
  }
  .header-col2 {
    width: 60%;
  }
  .logo {
    max-width: 80px;
  }
  .items-link,
  .header-btn {
    font-size: calc(var(--p3) - 3px);
  }
}
@media (max-width: 890px) {
  .header-div {
    height: 90px;
  }
  .header-col2 {
    width: 70%;
  }
  .logo {
    max-width: 70px;
  }
  .items-link,
  .header-btn {
    font-size: calc(var(--p3) - 5px);
    /* border: 2px solid red; */
  }
}
@media (max-width: 760px) {
  :root {
    --p3: 26px;
  }
  .header-div {
    width: 95%;
  }
  .header-col2 {
    display: flex;
    justify-content: flex-end;
  }
  .hamburger {
    display: flex;
  }
  .header-col3 {
    display: none;
  }
  .nav {
    /* border: 1px solid var(--white); */
    background-color: var(--bg2);
    transform: translateY(-100%);
    width: 70%;
    height: max-content;
    position: absolute;
    top: 0;
    right: 0;
    transition: transform 400ms;
    z-index: 10;
  }

  .nav.active {
    transform: translateY(0%);
  }
  .nav-list {
    margin-top: 5rem;
    flex-direction: column;
    margin-left: 2rem;
  }
  .nav-items {
    margin-bottom: 1rem;
  }
  .hide-items {
    display: block;
    margin-bottom: 2rem;
  }
  .hide-btn {
    background-color: transparent;
    padding: 12px 20px;
    outline: none;
    border: 1px solid var(--gumbo);
    border-radius: 50%;
    color: var(--white);
    font-size: var(--p2);
    cursor: pointer;
  }
  .items-link {
    color: var(--white);
    font-size: var(--p3);
  }
}
@media (max-width: 520px) {
  .nav {
    width: 80%;
  }
  .hide-btn {
    padding: 8px 14px;
    font-size: var(--p1);
  }
  .nav-items {
    margin-bottom: 0.5rem;
  }
  .hide-items {
    margin-bottom: 1.5rem;
  }
}

