  flex-direction: column;
  align-items: center;
}
.div1 {
  background-color: var(--bg1);
  margin-top: 1rem;
  padding: 4rem 2rem;
  width: 250px;
  height: 200px;
}
.div1 a {
  text-decoration: none;
  color: var(--gumbo);
}
.div1 a:hover {
  color: var(--white);
  border-bottom: 1px solid var(--gumbo);
}
.links {
  display: flex;
  justify-content: space-between;
}
.contact-link {
  border: 1px solid var(--white);
  padding: 0.5rem 0.7rem;
  padding-bottom: 0.4rem;
  border-radius: 50%;
}
.contact-icon {
  color: var(--white);
}
.contact-link:hover {
  background-color: var(--white);
}
.contact-link:hover .contact-icon {
  color: var(--gumbo);
}
.sec7-heading5,
.sec7-heading5-2 {
  color: var(--secondWhite);
  font-size: var(--p2);
  margin-bottom: 1.5rem;
}
.sec7-heading5:last-of-type {
  color: var(--gumbo);
}
.sec7-col2 {
}
.form {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
}
.form-input {
  outline: none;
  background-color: transparent;
  font-size: var(--h4);
  border: 1px solid var(--gumbo);
  padding: 1rem 0.5rem;
  color: var(--gumbo);
  margin-bottom: 1rem;
}
.textarea {
  font-size: var(--h4);
  outline: none;
  width: 100%;
  background-color: transparent;
  border: 1px solid var(--gumbo);
  color: var(--gumbo);
  padding: 1rem 0.5rem;
  margin-bottom: 1rem;
}
.form-input:hover,
.form-input:focus,
.textarea:hover,
.textarea:focus {
  border: 1px solid var(--white);
  color: white;
}

/* \\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\ Footer SECTION ///////////////*/
.footer {
  background-color: var(--bg1);
  border-top: 1px solid var(--gumbo);
  padding-bottom: 2rem;
}
.sec8 {
  padding: 1rem;
  display: flex;
  flex-direction: column;
}
.foot-logo {
  margin: 0 auto;
  order: 1;
